import actionCreatorFactory from 'typescript-fsa';
import { ApiBase } from '../../../../service/api-base';
import { CsvLostOrderType } from '../../../../type/csv/csv.type';

/* Param */
export type ApiCsvLostOrderGetListParam = Partial<{
  reception_date: string;
  category: number;
  supported_kubun: number;
  word: string;
  sp_word: string;
  customer_name: string;
  customer_responsible_store: number;
  customer_representative: number;
  supported_date: string;
  supported_responsible_store: number;
  supported_representative: number;
  project_name: string;
  date_time_start_year: string;
  date_time_start_month: string;
  date_time_start_date: string;
  date_time_end_year: string;
  date_time_end_month: string;
  date_time_end_date: string;
  subject: string;
  supported_person: number;
  supported_complete_start_year: string;
  supported_complete_start_month: string;
  supported_complete_start_date: string;
  supported_complete_end_year: string;
  supported_complete_end_month: string;
  supported_complete_end_date: string;
  is_fixed: number;
}>;

export type ApiCsvLostOrderDownloadParam = any;

/* Response */
export type ApiCsvLostOrderGetListResponse = CsvLostOrderType;

export const ActionCreator = actionCreatorFactory('csv/api/lost/order');

export const apiCsvLostOrder = {
  getList: ActionCreator<ApiCsvLostOrderGetListParam>('get/list'),
  download: ActionCreator<ApiCsvLostOrderDownloadParam>('download'),
};

export class ApiCsvLostOrderGetList extends ApiBase<
  ApiCsvLostOrderGetListResponse
> {
  constructor(param: ApiCsvLostOrderGetListParam) {
    super({
      httpMethod: 'GET',
      param,
      url: '/api/csv/lostorder',
    });
  }
}

export class ApiCsvLostOrderDownload extends ApiBase {
  constructor(param: ApiCsvLostOrderDownloadParam) {
    super({
      httpMethod: 'GET',
      contents: 'BLOB',
      param,
      url: '/api/csv/lostorder/download',
    });
  }
}
