import { DirectionsRenderer, DirectionsService } from '@react-google-maps/api';
import {
  useCallback, useEffect, useState,
} from 'react';
import { useDispatch } from 'react-redux';
import { Position } from '../../../../type/map/map.type';
import { SystemActions } from '../../../../redux/system/system.action';

type Props = {
  start: Position;
  end: Position;
  mode: globalThis.google.maps.TravelMode;
}

export const Direction = (props: Props) => {
  const {
    start,
    end,
    mode,
  } = props;

  const dispatch = useDispatch();

  const [
    currentDirection,
    setCurrentDirection,
  ] = useState<globalThis.google.maps.DirectionsResult | null>(null);

  const [
    viewPort,
    setViewPort,
  ] = useState(false);

  const routeCallback = useCallback(
    (res: globalThis.google.maps.DirectionsResult,
      status: globalThis.google.maps.DirectionsStatus) => {
      if (res) {
        const route = res.routes[0].legs[0];

        /* すでにルート状態があった場合 */
        if (currentDirection) {
          if (
            status === globalThis.google.maps.DirectionsStatus.OK
              && res.geocoded_waypoints?.length
              !== currentDirection.geocoded_waypoints?.length
          ) {
            setCurrentDirection(res);
            if (route.distance && route.duration) {
              window.console.log('ルート結果', route);
            }
          }
        } else if (status === globalThis.google.maps.DirectionsStatus.OK) {
          window.console.log('ルート結果', route);
          setCurrentDirection(res);
        }
      }
    },
    [currentDirection],
  );

  useEffect(() => {
    if (!currentDirection) {
      dispatch(SystemActions.isLoading(true));
      setTimeout(() => {
        setViewPort(true);
        dispatch(SystemActions.isLoading(false));
      }, 1000);
    }
  }, [currentDirection]);

  return (
    <div>
      {!currentDirection
      && (
      <DirectionsService
        options={{
          origin: start,
          destination: end,
          travelMode: mode,
        }}
        callback={routeCallback}
      />
      )}
      {currentDirection !== null && (
        <DirectionsRenderer
          options={{
            draggable: false,
            directions: currentDirection,
            preserveViewport: viewPort,
            suppressMarkers: true,
            polylineOptions: {
              clickable: false,
              strokeColor: 'red',
              strokeWeight: 7,
              strokeOpacity: 0.7,
            },
          }}
        />
      )}
    </div>
  );
};
