import { isEqual } from 'lodash';
import { useState, useCallback, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useDidMount, useWillUnMount } from '../../../../../../hooks/life-cycle';
import { CsvActions } from '../../../../../../redux/csv/csv.action';
import { State } from '../../../../../../redux/root.reducer';
import { Limit, TableSort } from '../../../../../ui/table/table-sort/table-sort';
import { CsvListEnquete } from '../../list/enquete/csv-list-enquete';
import { CsvSearchBoxEnquete } from '../../search-box/enquete/csv-search-box-enquete';

type Props = {
  callback: (v: () => void) => void;
}

export const CsvEnquete = (props:Props) => {
  const { callback } = props;

  /* Hooks */
  const dispatch = useDispatch();
  const sortState = useSelector((state: State) => (state.csv.enqueueSort), isEqual);
  const list = useSelector((state: State) => (state.csv.enqueueList), isEqual);

  /* State */
  // eslint-disable-next-line
  const [selected, setSelected] = useState<number[]>([]);
  // eslint-disable-next-line
  const [searchIsOpen, setSearchIsOpen] = useState<boolean>(false);

  /* Callback */
  const getList = useCallback(() => {
    dispatch(CsvActions.api.enqueue.getList({
      ...sortState,
    }));
    setSelected([]);
  },
  [sortState]);

  const exportCsv = useCallback(() => {
    dispatch(CsvActions.api.enqueue.download({ ...selected }));
  }, [selected]);

  const changePagination = useCallback((offset:number, limit:Limit) => {
    dispatch(CsvActions.setEnqueueSort({ offset, limit }));
  }, []);

  /* Effect */
  useEffect(() => {
    getList();
  }, [
    sortState.offset,
    sortState.order,
    sortState.limit,
    sortState.sort_by,
  ]);

  useEffect(() => {
    callback(exportCsv);
  }, [selected, exportCsv]);

  /* Master */
  useDidMount(() => {
  });

  useWillUnMount(() => {
    dispatch(CsvActions.setEnqueueSort(null));
  });

  return (
    <>
      {/* アンケート情報*/}
      <CsvSearchBoxEnquete
        callback={getList}
      />
      <TableSort
        page={sortState.offset ?? 0}
        limit={sortState.limit ?? 100}
        hitCount={list.length}
        callback={changePagination}
      />
      <CsvListEnquete
        callbackSelected={setSelected}
      />
    </>
  );
};
