import { cloneDeep } from 'lodash';
import { useState, useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Select } from '../../../../../ui/select/select';
import { RightLabelInputField } from '../../../../../ui/input-field/right-label-input-field/right-label-input-field';
import { LeftIconButton } from '../../../../../ui/button/left-icon-button/left-icon-button';
import { SearchBoxPC } from '../../../../layout/search-box/search-box.pc';
import { CsvActions } from '../../../../../../redux/csv/csv.action';
import { State } from '../../../../../../redux/root.reducer';
import { CsvEnqueueSort } from '../../../../../../type/csv/csv-sort.type';

type Props = {
  callback: () => void;
}

export const CsvSearchBoxEnquete = (props: Props) => {
  const { callback } = props;

  /* Hooks */
  // eslint-disable-next-line
  const dispatch = useDispatch();
  const sortState = useSelector((state: State) => state.csv.enqueueSort);

  /* State */
  const [sort, setSort] = useState(cloneDeep(sortState));

  /* Callback */
  // eslint-disable-next-line
  const setState = useCallback((v: CsvEnqueueSort) => {
    setSort(v);
    dispatch(CsvActions.setEnqueueSort(v));
  }, [sort]);

  const handleClickSearch = useCallback(() => {
    callback();
  }, [callback]);

  return (
    <>
      <SearchBoxPC
        openCallback={() => {}}
      >
        <>
          <div className="item_wrap">
            <div className="item_box">
              <Select
                className="add_text_left"
                label="店舗"
                value=""
                onChange={() => {}}
                options={[
                  { text: '全て', value: NaN },
                  { text: '店舗A', value: 1 },
                ]}
              />
              <Select
                className="add_text_left"
                label="顧客分類"
                value=""
                onChange={() => {}}
                options={[
                  { text: '指定なし', value: NaN },
                  { text: '長谷川太郎', value: 1 },
                ]}
              />
            </div>
          </div>
          <div className="item_wrap">
            <div className="item_box">
              <div className="item_head">案件契約日</div>
              <RightLabelInputField
                className="item_box"
                label="月"
                value=""
              />
              <RightLabelInputField
                className="item_box"
                label="日〜"
                value=""
              />
              <RightLabelInputField
                className="item_box"
                label="月"
                value=""
              />
              <RightLabelInputField
                className="item_box"
                label="日"
                value=""
              />
              <div className="item_wrap flex_no_wrap_box">
                <div className="item_box flex_grow_1">
                  <LeftIconButton
                    label="検索"
                    fontAwesomeClass="fas fa-search"
                    className="btn_search for_detail"
                    size="sm"
                    color="secondary"
                    onClick={handleClickSearch}
                  />
                </div>
              </div>
            </div>
          </div>
        </>
      </SearchBoxPC>
    </>
  );
};
