import { ApiBase, devHost } from '../../../../service/api-base';

export type ApiLoginParam = {
  mail_address: string;
  password: string;
  remember: 0 | 1;
};

export type ApiLoginResponse = ApiLoginParam;

export class ApiLogin extends ApiBase<ApiLoginResponse> {
  constructor(param: ApiLoginParam) {
    super({
      httpMethod: 'POST',
      url: '/api/auth/login',
      param,
      host: devHost,
    });
  }
}
