import {
  useCallback, useEffect, useState,
} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Table } from '../../../../../ui/table/table';
import './csv-list-enquete.scss';
import { State } from '../../../../../../redux/root.reducer';
import { ExportCsvCollection } from '../../../../../../collection/export-csv/export-csv.collection';
import { CsvActions } from '../../../../../../redux/csv/csv.action';
import { CsvEnqueteType } from '../../../../../../type/csv/csv.type';

type Props = {
  callbackSelected: (selected: number[]) => void;
}

export const CsvListEnquete = (props: Props) => {
  const { callbackSelected } = props;

  /* Hooks */
  const list = useSelector((state: State) => state.csv.enqueueList);
  const dispatch = useDispatch();

  /* State */
  const [selected, setSelected] = useState<number[]>([]);

  /* Callback */
  const handleClick = useCallback((row: CsvEnqueteType) => {
    setSelected([list.findIndex((v) => v.customer_id === row.customer_id)]);
  }, [list]);

  const handleClickHeader = useCallback(
    (highlow:0 | 1, sort_by: number) => {
      dispatch(CsvActions.setEnqueueSort({ highlow, sort_by }));
    }, [],
  );

  const handleDbClick = useCallback((row:CsvEnqueteType) => {
    // dispatch(push(`${RoutingPath.customerDetail}/${row.customer_id}`));
    setSelected([list.findIndex((v) => v.customer_id === row.customer_id)]);
  },
  [list]);

  useEffect(() => {
    callbackSelected(selected);
  }, [selected]);

  return (
    <section className="result_area list_area">
      <div className="inner">
        <div className="table_responsive">
          <Table
            className="table_selectable table_sortable"
            header={ExportCsvCollection.customerInformationHeader}
            onClickRow={handleClick}
            onDbClick={handleDbClick}
            sort={{ onClick: handleClickHeader }}
            onClickMulti={(v: CsvEnqueteType[]) => {
              setSelected(v.map((v2) => list.findIndex((v3) => v3.customer_id === v2.customer_id)));
            }}
            rowDataList={list}
            selectedTr={selected}
            lists={list.map(() => (
              [
                // v.id,
                // v.name,
                // v.tel_no,
                // v.prefecture,
                // v.city + v.address,
                // '○', // OB客
                // v.rank,
                // '2021/02/13', // 最終完工日
                // '1000000', // 総工事金額
                // '0', // 工事回数
                // '案件化', // 状況
                // v.sales_contact, // 営業担当

                '0',
                '1',
                '2',
                '3',
                '4',
                '5',
                '6',
              ]
            ))}
          />
        </div>
      </div>
    </section>
  );
};
