import { useCallback, useMemo, useState } from 'react';
import { Rnd, DraggableData } from 'react-rnd';
import './variable-div.scss';

type Props = {
  barPos: 'right' | 'left' | 'top' | 'bottom';
  children: HTMLElement | globalThis.JSX.Element;
  maxHeight?: number;
  maxWidth?: number;
  minHeight?: number;
  minWidth?: number;
}

/* 呼び出す親をrelativeにして */
export const VariableDiv = (props:Props) => {
  const {
    barPos,
    children,
    maxHeight,
    maxWidth,
    minHeight,
    minWidth,
  } = props;

  /* State */
  const [height, setHeight] = useState<number>(320);
  const [width, setWidth] = useState<number>(320);

  /* Memo */
  const position = useMemo(() => {
    switch (barPos) {
      case 'bottom':
        return { x: 0, y: height - 20 };
      case 'right':
        return { x: width - 20, y: 0 };
      default:
        return { x: 0, y: 0 };
    }
  }, [barPos, height, width]);

  const axis = useMemo(() => (barPos === 'right' || barPos === 'left' ? 'x' : 'y'), [barPos]);

  /* Callback */
  const handleDragBar = useCallback(
    (_: any, v: DraggableData) => {
      if (axis === 'x') {
        const isMinus = position.x > v.x;
        const x = isMinus ? position.x - v.x : v.x - position.x;
        setWidth(position.x + (isMinus ? -x : x) + (barPos === 'right' ? 20 : 0));
        return;
      }
      const isMinus = position.y > v.y;
      const y = isMinus ? position.y - v.y : v.y - position.y;
      setHeight(height + (isMinus ? -y : y) + (barPos === 'bottom' ? 20 : 0));
    },
    [axis, width, height, position.x, position.y, minHeight, minWidth, maxHeight, maxWidth],
  );

  return (
    <div
      className="VariableDiv"
    >
      <div>
        {children}
        <Rnd
          className="slide_bar"
          style={{ backgroundColor: 'orange' }}
          default={{
            ...position,
            height: axis === 'x' ? 320 : 20,
            width: axis === 'x' ? 20 : 320,
          }}
          dragAxis={axis}
          enableResizing={{
            top: false,
            left: false,
            right: false,
            bottom: false,
            bottomLeft: false,
            bottomRight: false,
            topRight: false,
            topLeft: false,
          }}
          bounds=".VariableDiv"
          onDrag={handleDragBar}
        >
          =
        </Rnd>
      </div>
    </div>
  );
};
