import actionCreatorFactory from 'typescript-fsa';
import { ApiBase } from '../../../../service/api-base';
import { ApiPasswordChangeParam, ApiPasswordCheckDateParam, ApiPasswordMailSendParam } from './api-password.type';

export const ActionCreator = actionCreatorFactory('auth/api/password');

export const apiPassword = {
  dialogChange: ActionCreator<ApiPasswordChangeParam>('dialog/change'),
  change: ActionCreator<ApiPasswordChangeParam>('change'),
  send: ActionCreator<{param:ApiPasswordMailSendParam, onSuccess:()=>void}>('send'),
  checkDate: ActionCreator<{
    param: ApiPasswordCheckDateParam,
    onSuccess:() => void;
    onError: () => void;
      }>('check/date'),
};

/* パスワード変更 */
export class ApiPasswordChange extends ApiBase {
  constructor(param: ApiPasswordChangeParam) {
    super({
      httpMethod: 'POST',
      url: '/api/common/chgpassword',
      param,
    });
  }
}

/* パスワード再設定メール送信 */
export class ApiPasswordMailSend extends ApiBase {
  constructor(param: ApiPasswordMailSendParam) {
    super({
      httpMethod: 'POST',
      url: '/api/common/sendpassword',
      param,
    });
  }
}

/* 有効期限確認 */
export class ApiPasswordCheckDate extends ApiBase {
  constructor(param: ApiPasswordCheckDateParam) {
    super({
      httpMethod: 'POST',
      url: '/api/common/checkDate',
      param,
    });
  }
}
