import actionCreatorFactory from 'typescript-fsa';
import { ApiBase } from '../../../../service/api-base';
import { CsvOrderListType } from '../../../../type/csv/csv.type';

/* Param */
export type ApiCsvOrderGetListParam = Partial<{
  reception_date: string;
  category: number;
  supported_kubun: number;
  word: string;
  sp_word: string;
  customer_name: string;
  customer_responsible_store: number;
  customer_representative: number;
  supported_date: string;
  supported_responsible_store: number;
  supported_representative: number;
  project_name: string;
  date_time_start_year: string;
  date_time_start_month: string;
  date_time_start_date: string;
  date_time_end_year: string;
  date_time_end_month: string;
  date_time_end_date: string;
  subject: string;
  supported_person: number;
  supported_complete_start_year: string;
  supported_complete_start_month: string;
  supported_complete_start_date: string;
  supported_complete_end_year: string;
  supported_complete_end_month: string;
  supported_complete_end_date: string;
  is_fixed: number;
}>;

export type ApiCsvOrderDownloadParam = any;

/* Response */
export type ApiCsvOrderGetListResponse = CsvOrderListType;

export const ActionCreator = actionCreatorFactory('csv/api/order');

export const apiCsvOrder = {
  getList: ActionCreator<ApiCsvOrderGetListParam>('get/list'),
  download: ActionCreator<ApiCsvOrderDownloadParam>('download'),
};

export class ApiCsvOrderGetList extends ApiBase<ApiCsvOrderGetListResponse> {
  constructor(param: ApiCsvOrderGetListParam) {
    super({
      httpMethod: 'GET',
      param,
      url: '/api/csv/order',
    });
  }
}

export class ApiCsvOrderDownload extends ApiBase {
  constructor(param: ApiCsvOrderDownloadParam) {
    super({
      httpMethod: 'GET',
      contents: 'BLOB',
      param,
      url: '/api/csv/order/download',
    });
  }
}
