import React, {
  useCallback, useEffect, useState,
} from 'react';
import { useDispatch } from 'react-redux';
import { Button } from '../../ui/button/button';
import { DialogActions } from '../../../redux/dialog/dialog.action';
import { EstimateActions } from '../../../redux/estimate/estimate.action';
import { Estimate, EstimateListType } from '../../../type/estimate/estimate.type';
import { EstimatePdfModule } from '../../../modules/pdf/estimate/estimate.pdf-module';
import { BillPdfModule } from '../../../modules/pdf/seikyu/bill.pdf-module';
import { EstimateShanaiPdfModule } from '../../../modules/pdf/estimate-shanai/estimate.pdf-module';
import { BillLitePdfModule } from '../../../modules/pdf/bill-lite/bill-lite.pdf-module';
import { MultiPrintPdfModule } from '../../../modules/pdf/multi-print.pdf-module';
import { Utiwake2PdfModule } from '../../../modules/pdf/seikyu/utiwake2.pdf-module';

export type PrintType =
  'estimate'
  | 'estimateIn'
  | 'claim'
  | 'claimIn'

type Props = {
  data: any;
  type: PrintType;
  estimate?: Estimate;
  estimateListData?: EstimateListType;
}

export const PrintPreview = (props: Props) => {
  const {
    // eslint-disable-next-line @typescript-eslint/no-unused-vars,no-unused-vars
    data, type, estimate, estimateListData,
  } = props;
  /* Hooke */
  const dispatch = useDispatch();
  /* State */
  const [index, setIndex] = useState(0);
  const [templateList, setTemplateList] = useState<string[]>([]);
  const handleClickPrint = useCallback(
    () => {
      dispatch(EstimateActions.api.print({
        id: 1,
        data: {
          files: [],
        },
      }));
      dispatch(DialogActions.pop());
    },
    [],
  );
  /* effect */
  useEffect(() => {
    // const module = new Sample01PdfModule();
    // module.createSvgList().then((svgList) => {
    //   setTemplateList([...svgList, ...svgList]);
    // });
    if (type === 'estimate') {
      new EstimatePdfModule().createSvgList().then((v) => {
        setTemplateList(v);
      });
    }
    if (type === 'estimateIn') {
      new EstimateShanaiPdfModule().createSvgList().then((v) => {
        setTemplateList(v);
      });
    }
    if (type === 'claimIn') {
      const mod = new MultiPrintPdfModule(
        new BillPdfModule(),
        new Utiwake2PdfModule(),
      );
      mod.createSvgList(
        undefined,
        new Utiwake2PdfModule().test(),
      ).then((v) => {
        setTemplateList(v);
      });
    }
    if (type === 'claim') {
      const mod = new BillLitePdfModule();
      mod.createSvgList().then((v) => {
        setTemplateList(v);
      });
    }
    setIndex(0);
  }, [type]);
  return (
    <div
      style={{
        maxHeight: '800px',
      }}
      className="PrintPreview"
    >
      <div
        className="preview_area"
        dangerouslySetInnerHTML={{ __html: (templateList.length && templateList[index]) || '' }}
      />
      <div className="base_footer">
        <div>
          ※印刷方向を
          <span style={{ color: 'red' }}> 横 </span>、
          用紙サイズを
          <span style={{ color: 'red' }}> 縦 </span>
          にしてください。
        </div>
        <div style={{ display: 'flex' }}>
          <div>
            <Button
              size="md"
              color="primary"
              onClick={handleClickPrint}
            >
              印刷
            </Button>
            <Button
              size="md"
              color="dark"
              onClick={() => dispatch(DialogActions.pop())}
            >
              キャンセル
            </Button>
          </div>
          <div>
            <Button
              disabled={!(index >= 1)}
              size="md"
              color="primary"
              onClick={() => setIndex(index - 1)}
            >
              前へ
            </Button>
            <Button
              disabled={!(index <= templateList.length - 2)}
              size="md"
              color="primary"
              onClick={() => setIndex(index + 1)}
            >
              次へ
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
};
