import React, {
  memo, useCallback, useEffect, useMemo, useState,
} from 'react';
import {
  Input as SInput,
  InputOnChangeData,
  InputProps as SInputProps,
} from 'semantic-ui-react';
import { Validation } from '../../../model/validation/validation';
import { ValidationNotEmpty } from '../../../model/validation/validation-not-empty';
import { ErrorPop } from '../error-pop/error-pop';
import './input.scss';
import { UserAgent } from '../../../utilities/user-agent';

export type InputProps = {
  validationList?: Validation[];
  alignRight?: boolean;
  onEnterKeyPress?: () => void;
  /**  触る前からエラー出すかどうか */
  touch?: boolean;
  require?: boolean;
  id?: string;
  name?: string;
  onBlur?: () => void;
  /** 任意文字列の場合string Valueの場合true */
  title?: string | boolean;
  noRequireLabel?: boolean;
  /** 半角英数字のみ対応 */
  maxLength?:number;
  onChangedBlur?: () => void;
  errorPosBottom?: boolean;
} & SInputProps

const InputComponent = (props: InputProps) => {
  const {
    value,
    validationList,
    disabled,
    onEnterKeyPress,
    onChange,
    placeholder,
    className,
    touch,
    alignRight,
    type,
    require,
    error,
    name,
    id,
    title,
    onBlur,
    maxLength,
    onChangedBlur,
    errorPosBottom,
  } = props;

  /* State */
  const [errorList, setErrorList] = useState<string[]>([]);
  const [changed, setChanged] = useState<boolean>(false);
  const [focus, setFocus] = useState<boolean>(false);

  /* Memo */
  const isError = useMemo(() => (
    Boolean((touch || changed) && errorList.length)
  ), [touch, changed, errorList]);

  const tip = useMemo(() => {
    if (typeof title === 'string') {
      return title;
    }
    return title ? value : undefined;
  }, [title, value]);

  /* Callback */
  const handleOnChange = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>, data: InputOnChangeData) => {
      setChanged(true);
      if (onChange) {
        if (maxLength) {
          if (event.target.value.length > maxLength) {
            return;
          }
        }
        onChange(event, data);
      }
    },
    [changed, onChange, value, maxLength],
  );

  const handleOnBlur = useCallback(() => {
    setChanged(true);
    setFocus(false);
    if (!validationList) {
      setErrorList(
        require ? ValidationNotEmpty
          .filter((v) => !v.run(value ? String(value) : ''))
          .map((v) => v.errorMessage)
          : [],
      );
    } else {
      setErrorList(
        require ? [...ValidationNotEmpty, ...validationList]
          .filter((v) => !v.run(value ? String(value) : ''))
          .map((v) => v.errorMessage)
          : validationList
            .filter((v) => !v.run(value ? String(value) : ''))
            .map((v) => v.errorMessage),
      );
    }
    if (onBlur) onBlur();
    if (touch && onChangedBlur) onChangedBlur();
  },
  [value, onBlur]);

  useEffect(() => {
    if (touch) {
      if (!validationList) {
        setErrorList(
          require ? ValidationNotEmpty
            .filter((v) => !v.run(value ? String(value) : ''))
            .map((v) => v.errorMessage)
            : [],
        );
      } else {
        setErrorList(
          require ? [...ValidationNotEmpty, ...validationList]
            .filter((v) => !v.run(value ? String(value) : ''))
            .map((v) => v.errorMessage)
            : validationList
              .filter((v) => !v || !v.run(value ? String(value) : ''))
              .map((v) => v.errorMessage),
        );
      }
    }
  }, [touch]);

  return (
    <div
      className={`
      base_input ${className || ''}
      ${UserAgent === 'pc' ? ' error_focus' : ''}
      `}
      onKeyPress={(e) => { if (e.key === 'Enter' && onEnterKeyPress) { onEnterKeyPress(); } }}
      onBlur={handleOnBlur}
      title={tip}
      onFocus={() => setFocus(true)}
    >
      <SInput
        className={`${alignRight ? 'align_r' : ''}`}
        error={isError || error}
        value={value || value === 0 ? String(value) : ''}
        disabled={disabled}
        onChange={handleOnChange}
        placeholder={placeholder}
        type={type}
        name={name || 'auto-off'}
        id={id || 'auto-off'}
        input={{ autoComplete: name || 'auto-off' }}
        // name={name}
        // id={id}
      />
      {/* Error Message */}
      {isError && (UserAgent === 'sp' ? focus : true) && (
        <ErrorPop messages={errorList} errorPosBottom={errorPosBottom} />
      )}
    </div>
  );
};

export const Input = memo(InputComponent);
